import { Client, createClient } from '../../generated/sitesClient';
import { Deployment, DeploymentRequest, Site, SiteRequest } from '../../generated/sitesClient/schema';
import { AuthenticatedClient, AuthenticatedClientOptions } from '../libs/AuthenticatedClient';

type SitesClientOptions = AuthenticatedClientOptions & {
  sitesServiceApiUrl: string;
};

export type GetSiteArgs = Pick<Site, 'id'>;
export type GetBySlugArgs = Pick<Site, 'slug'>;
export type CreateSiteArgs = Pick<Site, 'name'>;
export type DeleteSiteArgs = Pick<Site, 'id'>;
export type CreateCustomIpfsDeploymentArgs = Pick<Deployment, 'siteId' | 'cid'>;
export type GetDeploymentArgs = Pick<Deployment, 'id'>;

export class SitesClient extends AuthenticatedClient {
  private client?: Client;
  private sitesServiceApiUrl: string;
  private static SITE_MAPPED_PROPERTIES: SiteRequest = {
    id: true,
    name: true,
    slug: true,
    zones: { id: true },
    deployments: { id: true, createdAt: true, updatedAt: true, status: true },
  };
  private static DEPLOYMENT_MAPPED_PROPERTIES: DeploymentRequest = {
    id: true,
    status: true,
    storageType: true,
    siteId: true,
    cid: true,
    updatedAt: true,
    createdAt: true,
  };

  private getClient = (): Client => {
    if (!this.client) {
      this.client = createClient({
        url: this.sitesServiceApiUrl,
        headers: async () => this.getAuthenticationHeaders(),
      });
    }

    return this.client;
  };

  constructor(options: SitesClientOptions) {
    super(options);
    this.sitesServiceApiUrl = options.sitesServiceApiUrl;
  }

  public get = async ({ id }: GetSiteArgs): Promise<Site> => {
    const response = await this.getClient().query({
      site: [{ id }, SitesClient.SITE_MAPPED_PROPERTIES],
    });

    return response.site;
  };

  public getBySlug = async ({ slug }: GetBySlugArgs): Promise<Site> => {
    const response = await this.getClient().query({
      siteBySlug: [{ slug }, SitesClient.SITE_MAPPED_PROPERTIES],
    });

    return response.siteBySlug;
  };

  public list = async (): Promise<Site[]> => {
    const response = await this.getClient().query({
      sites: SitesClient.SITE_MAPPED_PROPERTIES,
    });

    return response.sites;
  };

  public create = async ({ name }: CreateSiteArgs): Promise<Site> => {
    const response = await this.getClient().mutation({
      createSite: [{ name }, SitesClient.SITE_MAPPED_PROPERTIES],
    });

    return response.createSite;
  };

  public delete = async ({ id }: DeleteSiteArgs): Promise<Site> => {
    const response = await this.getClient().mutation({
      deleteSite: [{ id }, SitesClient.SITE_MAPPED_PROPERTIES],
    });

    return response.deleteSite;
  };

  public createCustomIpfsDeployment = async ({ siteId, cid }: CreateCustomIpfsDeploymentArgs): Promise<Deployment> => {
    const response = await this.getClient().mutation({
      createCustomIpfsDeployment: [{ siteId, cid }, SitesClient.DEPLOYMENT_MAPPED_PROPERTIES],
    });

    return response.createCustomIpfsDeployment;
  };

  public getDeployment = async ({ id }: GetDeploymentArgs): Promise<Deployment> => {
    const response = await this.getClient().query({
      deployment: [{ id }, SitesClient.DEPLOYMENT_MAPPED_PROPERTIES],
    });

    return response.deployment;
  };
}
