import { Client, createClient } from '../../generated/graphqlClient';
import { AuthenticatedClient, AuthenticatedClientOptions } from '../libs/AuthenticatedClient';

type UserClientOptions = AuthenticatedClientOptions & {
  graphqlServiceApiUrl: string;
};

export class UserClient extends AuthenticatedClient {
  private client?: Client;
  private graphqlServiceApiUrl: string;

  private getClient = (): Client => {
    if (!this.client) {
      this.client = createClient({
        url: this.graphqlServiceApiUrl,
        headers: async () => this.getAuthenticationHeaders(),
      });
    }

    return this.client;
  };

  constructor(options: UserClientOptions) {
    super(options);
    this.graphqlServiceApiUrl = options.graphqlServiceApiUrl;
  }

  public listPersonalAccessTokens = async () => {
    const response = await this.getClient().query({ personalAccessTokens: { id: true, createdAt: true, name: true, maskedToken: true } });

    return response.personalAccessTokens;
  };

  public deletePersonalAccessToken = async ({ id }: { id: string }) => {
    const response = await this.getClient().mutation({ deletePersonalAccessToken: [{ id }] });

    return response.deletePersonalAccessToken;
  };
}
