import { Client, createClient, DomainRequest, ZoneRequest } from '../../generated/domainsClient';
import { AuthenticatedClient, AuthenticatedClientOptions } from '../libs/AuthenticatedClient';
import { graphqlFetcher } from '../libs/graphqlFetcher';

type DomainsClientOptions = AuthenticatedClientOptions & {
  domainsServiceApiUrl: string;
};

export class DomainsClient extends AuthenticatedClient {
  private client?: Client;
  private domainsServiceApiUrl: string;
  private static DOMAIN_MAPPED_PROPERTIES: DomainRequest = {
    id: true,
    zoneId: true,
    hostname: true,
    isVerified: true,
    updatedAt: true,
    createdAt: true,
    dnsConfigs: { id: true, type: true, name: true, value: true, createdAt: true, updatedAt: true },
    status: true,
  };
  private static ZONE_MAPPED_PROPERTIES: ZoneRequest = { id: true, originUrl: true, createdAt: true, updatedAt: true };

  private getClient = (): Client => {
    if (!this.client) {
      this.client = createClient({
        fetcher: async (operation) =>
          graphqlFetcher({ operation, headers: await this.getAuthenticationHeaders(), endpoint: this.domainsServiceApiUrl }),
      });
    }

    return this.client;
  };

  constructor(options: DomainsClientOptions) {
    super(options);
    this.domainsServiceApiUrl = options.domainsServiceApiUrl;
  }

  public list = async () => {
    const response = await this.getClient().query({ domains: DomainsClient.DOMAIN_MAPPED_PROPERTIES });

    return response.domains;
  };

  public get = async ({ domainId }: { domainId: string }) => {
    const response = await this.getClient().query({ domain: [{ domainId }, DomainsClient.DOMAIN_MAPPED_PROPERTIES] });

    return response.domain;
  };

  public getByHostname = async ({ hostname }: { hostname: string }) => {
    const response = await this.getClient().query({ domainByHostname: [{ hostname }, DomainsClient.DOMAIN_MAPPED_PROPERTIES] });

    return response.domainByHostname;
  };

  public listByZoneId = async ({ zoneId }: { zoneId: string }) => {
    const response = await this.getClient().query({ domainsByZoneId: [{ zoneId }, DomainsClient.DOMAIN_MAPPED_PROPERTIES] });

    return response.domainsByZoneId;
  };

  public createDomain = async ({ zoneId, hostname }: { zoneId: string; hostname: string }) => {
    const response = await this.getClient().mutation({ createDomain: [{ zoneId, hostname }, DomainsClient.DOMAIN_MAPPED_PROPERTIES] });

    return response.createDomain;
  };

  public deleteDomain = async ({ domainId }: { domainId: string }) => {
    const response = await this.getClient().mutation({ deleteDomain: [{ domainId }, DomainsClient.DOMAIN_MAPPED_PROPERTIES] });

    return response.deleteDomain;
  };

  public verifyDomain = async ({ domainId }: { domainId: string }) => {
    const response = await this.getClient().mutation({ verifyDomain: [{ domainId }, DomainsClient.DOMAIN_MAPPED_PROPERTIES] });

    return response.verifyDomain;
  };

  public listZones = async () => {
    const response = await this.getClient().query({ zones: DomainsClient.ZONE_MAPPED_PROPERTIES });

    return response.zones;
  };

  public getZone = async ({ id }: { id: string }) => {
    const response = await this.getClient().query({ zone: [{ id }, DomainsClient.ZONE_MAPPED_PROPERTIES] });

    return response.zone;
  };

  public createZoneForSite = async ({ siteId }: { siteId: string }) => {
    const response = await this.getClient().mutation({ createZoneForSite: [{ siteId }, DomainsClient.ZONE_MAPPED_PROPERTIES] });

    return response.createZoneForSite;
  };
}
