import { constants } from '@fleekxyz/auth';
import { AuthorizationError, StorageIpfsUploadFailedError } from '@fleekxyz/errors';
import { CID, create, globSource, IPFSHTTPClient } from 'ipfs-http-client';

import { AuthenticatedClient, AuthenticatedClientOptions } from '../libs/AuthenticatedClient';
import { requireNodeEnv } from '../libs/requireNodeEnv';

export type IpfsFile = {
  path: string;
  content?: Buffer;
};

export type IpfsClientOptions = AuthenticatedClientOptions & {
  ipfsStorageApiUrl: string;
};

export type AddAllOptions = {
  wrapWithDirectory?: boolean;
};

export type AddFromPathOptions = {
  wrapWithDirectory?: boolean;
};

export type UploadResult = {
  cid: CID;
  size: number;
  path: string;
};
export class IpfsClient extends AuthenticatedClient {
  private client?: IPFSHTTPClient;
  private ipfsStorageApiUrl: string;

  private getClient = (): IPFSHTTPClient => {
    if (!this.client) {
      this.client = create({
        url: this.ipfsStorageApiUrl,
      });
    }

    return this.client;
  };

  constructor(options: IpfsClientOptions) {
    super(options);
    this.ipfsStorageApiUrl = options.ipfsStorageApiUrl;
  }

  public add = async (file: IpfsFile): Promise<UploadResult> => {
    const accessToken = await this.getAccessToken();

    if (!accessToken) {
      throw new AuthorizationError();
    }

    try {
      return this.getClient().add(file, {
        headers: {
          [constants.AUTHORIZATION_HEADER_NAME]: `Bearer ${accessToken}`,
        },
      });
    } catch (err) {
      throw new StorageIpfsUploadFailedError();
    }
  };

  public addAll = async (files: Array<IpfsFile> | ReturnType<typeof globSource>, options: AddAllOptions = {}): Promise<UploadResult[]> => {
    const accessToken = await this.getAccessToken();

    if (!accessToken) {
      throw new AuthorizationError();
    }

    try {
      const iterable = this.getClient().addAll(files, {
        headers: {
          [constants.AUTHORIZATION_HEADER_NAME]: `Bearer ${accessToken}`,
        },
        ...options,
      });

      const added: UploadResult[] = [];
      for await (const file of iterable) {
        added.push(file);
      }

      return added;
    } catch (err) {
      throw new StorageIpfsUploadFailedError();
    }
  };

  public addFromPath = async (path: string, options: AddFromPathOptions = {}) => {
    requireNodeEnv();

    return this.addAll(globSource(path, '**/*'), options);
  };
}
