import { Client, createClient } from '../../generated/graphqlClient';
import { AuthenticatedClient, AuthenticatedClientOptions } from '../libs/AuthenticatedClient';

type ProjectsClientOptions = AuthenticatedClientOptions & {
  graphqlServiceApiUrl: string;
};

export class ProjectsClient extends AuthenticatedClient {
  private client?: Client;
  private graphqlServiceApiUrl: string;

  private getClient = (): Client => {
    if (!this.client) {
      this.client = createClient({
        url: this.graphqlServiceApiUrl,
        headers: async () => this.getAuthenticationHeaders(),
      });
    }

    return this.client;
  };

  constructor(options: ProjectsClientOptions) {
    super(options);
    this.graphqlServiceApiUrl = options.graphqlServiceApiUrl;
  }

  public create = async ({ name }: { name: string }) => {
    const response = await this.getClient().mutation({ createProject: [{ name }, { id: true, name: true }] });

    return response.createProject;
  };

  public get = async ({ id }: { id: string }) => {
    const response = await this.getClient().query({ project: [{ id }, { id: true, name: true }] });

    return response.project;
  };

  public list = async () => {
    const response = await this.getClient().query({ projects: { id: true, name: true } });

    return response.projects;
  };
}
