import { AuthorizationError } from '@fleekxyz/errors';

import { Client, createClient } from '../../../generated/graphqlClient';
import { defined } from '../../defined';
import { AccessTokenService } from './AccessTokenService';

type Web3AuthAccessTokenServiceOptions = {
  graphqlServiceApiUrl?: string;
  projectId?: string;
  web3AuthToken: string;
};

export class Web3AuthAccessTokenService extends AccessTokenService {
  private client: Client;
  private projectId?: string;
  private accessToken?: string;
  private web3AuthToken: string;
  private accessTokenTimeout?: NodeJS.Timeout;

  constructor({ web3AuthToken, projectId, graphqlServiceApiUrl = defined.SDK__GRAPHQL_API_URL }: Web3AuthAccessTokenServiceOptions) {
    super();
    this.projectId = projectId;
    this.web3AuthToken = web3AuthToken;
    this.client = createClient({ url: graphqlServiceApiUrl });
  }

  private loginWithWeb3Auth = async () => {
    if (!this.web3AuthToken) {
      throw new AuthorizationError();
    }

    const { loginWithWeb3Auth } = await this.client.mutation({
      loginWithWeb3Auth: [{ projectId: this.projectId, idToken: this.web3AuthToken }],
    });
    this.setAccessToken({ token: loginWithWeb3Auth });
  };

  protected setAccessToken = async ({ token, expiration = 60 * 8 * 1000 }: { token: string; expiration?: number }) => {
    this.accessToken = token;

    if (this.accessTokenTimeout) {
      clearTimeout(this.accessTokenTimeout);
    }

    // 8 min timeout
    this.accessTokenTimeout = setTimeout(() => {
      this.accessToken = undefined;
    }, expiration);
  };

  public getAccessToken = async () => {
    if (!this.accessToken) {
      await this.loginWithWeb3Auth();
    }

    return this.accessToken!;
  };
}
