import { AuthorizationError } from '@fleekxyz/errors';

import { DomainsClient } from './clients/domains';
import { IpfsClient } from './clients/ipfs';
import { IpnsClient } from './clients/ipns';
import { ProjectsClient } from './clients/projects';
import { SitesClient } from './clients/sites';
import { UserClient } from './clients/user';
import { defined } from './defined';
import { AccessTokenService } from './libs/AccessTokenService/AccessTokenService';

export * from './libs/AccessTokenService/PersonalAccessTokenService';
export * from './libs/AccessTokenService/StaticAccessTokenService';
export * from './libs/AccessTokenService/Web3authAccessTokenService';

type FleekSdkOptions = {
  projectId?: string | undefined;
  ipnsServiceApiUrl?: string;
  graphqlServiceApiUrl?: string;
  sitesServiceApiUrl?: string;
  ipfsStorageApiUrl?: string;
  domainsServiceApiUrl?: string;
  accessTokenService: AccessTokenService;
};

export class FleekSdk {
  private accessTokenService: AccessTokenService;

  private userClient?: UserClient;
  private projectsClient?: ProjectsClient;
  private graphqlServiceApiUrl: string;

  private ipnsClient?: IpnsClient;
  private ipnsServiceApiUrl: string;

  private ipfsClient?: IpfsClient;
  private ipfsStorageApiUrl: string;

  private sitesClient?: SitesClient;
  private sitesServiceApiUrl: string;

  private domainsClient?: DomainsClient;
  private domainsServiceApiUrl: string;

  constructor({
    ipnsServiceApiUrl = defined.SDK__IPNS__API_URL,
    graphqlServiceApiUrl = defined.SDK__GRAPHQL_API_URL,
    sitesServiceApiUrl = defined.SDK__SITES__API_URL,
    ipfsStorageApiUrl = defined.SDK__IPFS__STORAGE_API_URL,
    domainsServiceApiUrl = defined.SDK__DOMAINS__API_URL,
    accessTokenService,
  }: FleekSdkOptions) {
    if (!accessTokenService) {
      throw new AuthorizationError();
    }

    this.accessTokenService = accessTokenService;

    this.graphqlServiceApiUrl = graphqlServiceApiUrl ?? defined.SDK__GRAPHQL_API_URL;
    this.domainsServiceApiUrl = domainsServiceApiUrl ?? defined.SDK__DOMAINS__API_URL;
    this.ipnsServiceApiUrl = ipnsServiceApiUrl ?? defined.SDK__IPNS__API_URL;
    this.sitesServiceApiUrl = sitesServiceApiUrl ?? defined.SDK__SITES__API_URL;
    this.ipfsStorageApiUrl = ipfsStorageApiUrl ?? defined.SDK__IPFS__STORAGE_API_URL;
  }

  public user = (): UserClient => {
    if (!this.userClient) {
      this.userClient = new UserClient({
        accessTokenService: this.accessTokenService,
        graphqlServiceApiUrl: this.graphqlServiceApiUrl,
      });
    }

    return this.userClient;
  };

  public ipns = (): IpnsClient => {
    if (!this.ipnsClient) {
      this.ipnsClient = new IpnsClient({
        accessTokenService: this.accessTokenService,
        ipnsServiceApiUrl: this.ipnsServiceApiUrl,
      });
    }

    return this.ipnsClient;
  };

  public ipfs = (): IpfsClient => {
    if (!this.ipfsClient) {
      this.ipfsClient = new IpfsClient({
        accessTokenService: this.accessTokenService,
        ipfsStorageApiUrl: this.ipfsStorageApiUrl,
      });
    }

    return this.ipfsClient;
  };

  public sites = (): SitesClient => {
    if (!this.sitesClient) {
      this.sitesClient = new SitesClient({
        accessTokenService: this.accessTokenService,
        sitesServiceApiUrl: this.sitesServiceApiUrl,
      });
    }

    return this.sitesClient;
  };

  public projects = (): ProjectsClient => {
    if (!this.projectsClient) {
      this.projectsClient = new ProjectsClient({
        accessTokenService: this.accessTokenService,
        graphqlServiceApiUrl: this.graphqlServiceApiUrl,
      });
    }

    return this.projectsClient;
  };

  public domains = (): DomainsClient => {
    if (!this.domainsClient) {
      this.domainsClient = new DomainsClient({
        accessTokenService: this.accessTokenService,
        domainsServiceApiUrl: this.domainsServiceApiUrl,
      });
    }

    return this.domainsClient;
  };
}
