import { Client, createClient } from '../../generated/ipnsClient';
import { AuthenticatedClient, AuthenticatedClientOptions } from '../libs/AuthenticatedClient';

type IpnsClientOptions = AuthenticatedClientOptions & {
  ipnsServiceApiUrl: string;
};

export type PublishRecord = {
  id: string;
  hash: string;
};

export type ResolveName = {
  name: string;
};

export type DeleteRecordArgs = {
  id: string;
};

export type GetRecordArgs = {
  name: string;
};

export type PublishSignedNameArgs = {
  input: string;
  key: string;
};

export class IpnsClient extends AuthenticatedClient {
  private client?: Client;
  private ipnsServiceApiUrl: string;

  private getClient = (): Client => {
    if (!this.client) {
      this.client = createClient({
        url: this.ipnsServiceApiUrl,
        headers: async () => this.getAuthenticationHeaders(),
      });
    }

    return this.client;
  };

  constructor(options: IpnsClientOptions) {
    super(options);
    this.ipnsServiceApiUrl = options.ipnsServiceApiUrl;
  }

  public publishSignedName = async ({ key, input }: PublishSignedNameArgs) => {
    const response = await this.getClient().mutation({
      publishSignedName: [{ input, key }],
    });

    return response.publishSignedName;
  };

  public resolveName = async ({ name }: ResolveName) => {
    const response = await this.getClient().query({
      resolveName: [{ name }],
    });

    return response.resolveName;
  };

  public publishRecord = async ({ hash, id }: PublishRecord) => {
    const response = await this.getClient().mutation({
      publishRecord: [
        { hash, id },
        {
          id: true,
          name: true,
          hash: true,
        },
      ],
    });

    return response.publishRecord;
  };

  public createRecord = async () => {
    const response = await this.getClient().mutation({
      createRecord: {
        id: true,
        name: true,
        hash: true,
      },
    });

    return response.createRecord;
  };

  public deleteRecord = async ({ id }: DeleteRecordArgs) => {
    const response = await this.getClient().mutation({
      deleteRecord: [
        { id },
        {
          id: true,
          name: true,
          hash: true,
        },
      ],
    });

    return response.deleteRecord;
  };

  public listRecords = async () => {
    const response = await this.getClient().query({
      records: {
        id: true,
        name: true,
        hash: true,
      },
    });

    return response.records;
  };

  public getRecord = async ({ name }: GetRecordArgs) => {
    const response = await this.getClient().query({
      record: [{ name }, { id: true, name: true, hash: true }],
    });

    return response.record;
  };
}
