import { UnauthorizedError, UnknownError } from '@fleekxyz/errors';
import * as errors from '@fleekxyz/errors';
import { GraphqlErrorPayload } from '@fleekxyz/graphql';
import { GraphqlOperation } from '@genql/runtime';
import axios from 'axios';
import _ from 'lodash';

type GraphqlFetcherArgs = {
  operation: GraphqlOperation | GraphqlOperation[];
  endpoint: string;
  headers: Record<string, any>;
};

export const graphqlFetcher = async ({ endpoint, headers, operation }: GraphqlFetcherArgs) =>
  axios
    .post<GraphqlErrorPayload>(endpoint, operation, {
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
        ...headers,
      },
    })
    .then((response) => {
      const data = response.data;

      if (!data.error) {
        return data;
      }

      const errorClass = _.get(errors, data.error.name);

      if (errorClass) {
        throw new errorClass(data.error.data);
      }

      throw new UnknownError();
    })
    .catch((e) => {
      if (e.response.status === 401) {
        throw new UnauthorizedError();
      }

      throw new UnknownError();
    });
