import { constants } from '@fleekxyz/auth';
import { Headers } from '@genql/runtime/dist/client/createClient';

import { AccessTokenService } from './AccessTokenService/AccessTokenService';

export type AuthenticatedClientOptions = {
  accessTokenService: AccessTokenService;
};

export abstract class AuthenticatedClient {
  private accessTokenService;

  constructor({ accessTokenService }: AuthenticatedClientOptions) {
    this.accessTokenService = accessTokenService;
  }

  protected getAccessToken = async () => this.accessTokenService.getAccessToken();

  protected getAuthenticationHeaders = async () => {
    const accessToken = await this.getAccessToken();

    if (!accessToken) {
      return {};
    }

    const headers: Headers = {
      [constants.AUTHORIZATION_HEADER_NAME]: `Bearer ${accessToken}`,
    };

    return headers;
  };
}
